import React from 'react';
import Grid from '@mui/material/Grid';
import PropTypes from 'prop-types';
import TabsComp from '../../containers/Common/Tab';
import TabPane from '../../containers/Common/TabPane';
import style from './EquipmentDetails.module.css';
import EquipmentData from '../../containers/EquipmentDetailStatus/EquipmentData';
const EquipmentDetails = ({ props }) => {
  const { count, decreaseCounter, increaseCounter } = props;
  return (
    <TabsComp>
      <TabPane name="Equipment List" key="1">
        <Grid container className={style.fullHeight}>
          <Grid item xs={12} md={9}>
            <div className={style.summarySection}>
              <EquipmentData />
            </div>
            <div>Count: {count}</div>
            <button
              type="button"
              onClick={() => {
                increaseCounter();
              }}
            >
              Increase Count
            </button>
            <button type="button" onClick={() => decreaseCounter()}>
              Decrease Count
            </button>
          </Grid>
          <Grid item xs={12} md={3} pl={{ sm: 0, md: 2 }} pt={{ sm: 2, md: 0 }}>
            <div className={style.summarySection}>Installed Base Summary</div>
          </Grid>
        </Grid>
      </TabPane>
      <TabPane name="Customer Status" key="2">
        <Grid container className={style.fullHeight}>
          <Grid item xs={12} md={9}>
            <div className={style.summarySection}>Customer Status Section</div>
          </Grid>
          <Grid item xs={12} md={3} pl={{ sm: 0, md: 2 }} pt={{ sm: 2, md: 0 }}>
            <div className={style.summarySection}>Customer Status Summary</div>
          </Grid>
        </Grid>
      </TabPane>
      <TabPane name="Equipment Placement Map" key="3">
        <Grid container className={style.fullHeight}>
          <Grid item xs={12} md={9}>
            <div className={style.summarySection}>
              Equipment Placement Map Section
            </div>
          </Grid>
          <Grid item xs={12} md={3} pl={{ sm: 0, md: 2 }} pt={{ sm: 2, md: 0 }}>
            <div className={style.summarySection}>Installed Base Summary</div>
          </Grid>
        </Grid>
      </TabPane>
    </TabsComp>
  );
};
EquipmentDetails.propTypes = {
  count: PropTypes.number,
  decreaseCounter: PropTypes.func,
  increaseCounter: PropTypes.func,
  props: PropTypes.object,
};
EquipmentDetails.defaultProps = {
  count: 0,
  decreaseCounter: () => {},
  increaseCounter: () => {},
  props: {},
};
export default EquipmentDetails;
